@media only screen and (max-width:380px){
    .info-wrapper .main-words{
        font-size: 1.5em;
        margin-top: 20px;
    }
    .main-content{
        font-size: 14px;
        width: 100%;
    }
    .media-box{
        flex-direction: column;
        align-items: unset !important;
    }
    .hire-btn{
        max-width : 150px;
        font-size: 12px;
    }
    .social-icons{
        margin-top: 10px;
        padding-left: 0px !important;
    }
    .info-wrapper{
        min-height: 60vh;
        padding-top: 10vh;
      }
      .portfolio-wrapper{
          padding: 10vh 0px;
      }
}
@media only screen and (max-width:720px) and (min-width:381px){
    .info-wrapper .main-words{
        font-size: 2.5em;
    }
    .media-box{
        flex-direction: column;
        align-items: unset !important;
    }
    .hire-btn{
        max-width : 150px;
        font-size: 12px;
    }
    .social-icons{
        margin-top: 10px;
        padding-left: 0px !important;
    }
    .info-wrapper{
        min-height: 60vh;
        padding-top: 10vh;
      }
      .portfolio-wrapper{
          padding: 10vh 0px;
      }
}
@media only screen and (max-width:1199px) and (min-width:720px){
    .info-wrapper .main-words{
        font-size: 3.5em;
    }
}