@import url(https://fonts.googleapis.com/css2?family=Space+Grotesk&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Roboto+Condensed:wght@700&display=swap);
body {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  overflow-x: hidden;
  font-family: 'Space Grotesk', sans-serif;
  background-color: rgb(240, 245, 247) !important;
}


.info-wrapper{
  min-height: 100vh;
  max-height: 100vh;
  display: flex;
  justify-content: center;
  flex-direction: column;
  /* position: fixed;
  width: 45vw; */
}
.info-wrapper .main-words{
  font-size: 4.5em;
  font-weight: bold;
  font-family: 'Roboto Condensed', sans-serif;
  background: linear-gradient(90deg, rgb(247, 32, 182), blue);
  -webkit-background-clip: text;
  -moz-background-clip: text;
  -webkit-text-fill-color: transparent; 
  -moz-text-fill-color: transparent;
}
.main-content{
  font-size: 14px;
  width: 60%;
}
.info-wrapper .name{
  font-size: 20px;
  font-weight: bold;
}
.greyLine{
  height: 3px ;
  width: 80px;
  border-radius: 5px;
  background: linear-gradient(90deg, rgb(247, 32, 182), blue);
}
.hire-btn{
  width: auto;
  max-width : 150px;
  min-width: 80px;
  background-color: #fff;
  border: none;
  border-radius: 6px;
  padding: 7px 20px;
  border: 1px solid rgb(204, 32, 247);
  font-weight: bold;
  background: linear-gradient(90deg, rgb(247, 32, 182), blue);
  -webkit-background-clip: text;
  -moz-background-clip: text;
  -webkit-text-fill-color: transparent; 
  -moz-text-fill-color: transparent;
  transition: .7s;
}
.hire-btn:hover{
  -webkit-background-clip: border-box;
  -moz-background-clip: border-box;
  -webkit-text-fill-color: #fff; 
  -moz-text-fill-color: #fff;
}
.media-box i{
  font-size: 20px;
  margin-right: 15px;
  padding-left: 10px;
}
.portfolio-wrapper{
  height: 100vh;
  padding: 10vh 20px;
  overflow-y: scroll;
  /* padding-top: 20vh; */
}
.portfolio-wrapper .portfolio-heading{
  font-size: 20px;
  font-weight: bold;
}
.portfolio{
  background-color: #fff;
  padding: 15px 20px;
  border-radius: 10px;
  box-shadow: 0px 0px 5px rgb(192, 192, 192);
  text-decoration: none !important;
  transition: .7s;
}
.portfolio:hover{
  box-shadow: 0px 0px 1px rgb(192, 192, 192);
}
.portfolio a{
  text-decoration: none !important;
}
.portfolio-wrapper::-webkit-scrollbar {
  /* width: 6px;
  background-color: transparent; */
  display: none;
}
.portfolio p{
  margin: 8px 0px;
}
.portfolio .projectName{
  background: linear-gradient(90deg, rgb(247, 32, 182), blue);
  -webkit-background-clip: text;
  -moz-background-clip: text;
  -webkit-text-fill-color: transparent; 
  -moz-text-fill-color: transparent;
  font-weight: bold;
  font-size: 18px;
}
.portfolio .projectTechUsed{
  margin: 10px 0px;
  font-size: 13px;
}
.portfolio .projectTech{
  font-size: 12px;
  font-weight: bold
}
.project-cat{
  font-weight: bold;
  font-size: 15px;
}
@media only screen and (max-width:380px){
    .info-wrapper .main-words{
        font-size: 1.5em;
        margin-top: 20px;
    }
    .main-content{
        font-size: 14px;
        width: 100%;
    }
    .media-box{
        flex-direction: column;
        align-items: unset !important;
    }
    .hire-btn{
        max-width : 150px;
        font-size: 12px;
    }
    .social-icons{
        margin-top: 10px;
        padding-left: 0px !important;
    }
    .info-wrapper{
        min-height: 60vh;
        padding-top: 10vh;
      }
      .portfolio-wrapper{
          padding: 10vh 0px;
      }
}
@media only screen and (max-width:720px) and (min-width:381px){
    .info-wrapper .main-words{
        font-size: 2.5em;
    }
    .media-box{
        flex-direction: column;
        align-items: unset !important;
    }
    .hire-btn{
        max-width : 150px;
        font-size: 12px;
    }
    .social-icons{
        margin-top: 10px;
        padding-left: 0px !important;
    }
    .info-wrapper{
        min-height: 60vh;
        padding-top: 10vh;
      }
      .portfolio-wrapper{
          padding: 10vh 0px;
      }
}
@media only screen and (max-width:1199px) and (min-width:720px){
    .info-wrapper .main-words{
        font-size: 3.5em;
    }
}
